@font-face {
  font-family: 'source_sans_proregular';
  src: url('../fonts/sourcesanspro-regular-webfont.eot');
  src: url('../fonts/sourcesanspro-regular-webfont.eot?#iefix') format("embedded-opentype"), url('../fonts/sourcesanspro-regular-webfont.woff2') format("woff2"), url('../fonts/sourcesanspro-regular-webfont.woff') format("woff"), url('../fonts/sourcesanspro-regular-webfont.ttf') format("truetype"), url('../fonts/sourcesanspro-regular-webfont.svg#source_sans_proregular') format("svg");
  font-weight: normal;
  font-style: normal;
}


body {
  font-family: 'source_sans_proregular', sans-serif;
  font-weight: 300;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0
}

*:before, *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
    padding: 0
}

html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.wrapper {
  width: 60rem;
  height: 20rem;
  margin-top: 5rem;
  margin-left: auto;
  margin-right: auto;
  -webkit-transition: -webkit-transform 1.5s;
  transition: -webkit-transform 1.5s;
  -o-transition: transform 1.5s;
  transition: transform 1.5s;
  transition: transform 1.5s, -webkit-transform 1.5s;
  -webkit-perspective: 3000px;
          perspective: 3000px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.page {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transform: rotateX(180deg) scale(0.3);
          transform: rotateX(180deg) scale(0.3);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transition: -webkit-transform 1s ease-in-out;
  transition: -webkit-transform 1s ease-in-out;
  -o-transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
  will-change: transform;
  -webkit-box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.75);
          box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.75);
  border-radius: 4px;
  @nest h2 {
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    font-size: 3em;
  }
}

.page.page1 {
    background-color: #66a6b8;
    background-image: -o-linear-gradient(80deg, #66a6b8 20%, #5471B9 80%);
    background-image: linear-gradient(10deg, #66a6b8 20%, #5471B9 80%)
}

.page.page2 {
    background-color: #f29c54;
    background-image: -o-linear-gradient(45deg, #f29c54 0%, #DB4367 100%);
    background-image: linear-gradient(45deg, #f29c54 0%, #DB4367 100%)
}

.page.page3 {
    background-color: #23af56;
    background-image: -o-linear-gradient(315deg, #23af56 0%, #67A79A 100%);
    background-image: linear-gradient(135deg, #23af56 0%, #67A79A 100%)
}

.page.page4 {
    background-color: #412F2F;
    background-image: -o-linear-gradient(290deg, #412F2F 15%, #6B2686 85%);
    background-image: linear-gradient(160deg, #412F2F 15%, #6B2686 85%)
}

.wrapper.active-page1 {
  -webkit-transform: translateY(0%);
      -ms-transform: translateY(0%);
          transform: translateY(0%);
}

.active-page1 .page1 {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.wrapper.active-page2 {
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}

.active-page2 .page2 {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.wrapper.active-page3 {
  -webkit-transform: translateY(-200%);
      -ms-transform: translateY(-200%);
          transform: translateY(-200%);
}

.active-page3 .page3 {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.wrapper.active-page4 {
  -webkit-transform: translateY(-300%);
      -ms-transform: translateY(-300%);
          transform: translateY(-300%);
}

.active-page4 .page4 {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.nav-panel {
  position: fixed;
  top: 50%;
  right: 2%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 1000;
  -webkit-transition: opacity 0.5s, -webkit-transform 0.5s cubic-bezier(0.57, 1.2, 0.68, 2.6);
  transition: opacity 0.5s, -webkit-transform 0.5s cubic-bezier(0.57, 1.2, 0.68, 2.6);
  -o-transition: opacity 0.5s, transform 0.5s cubic-bezier(0.57, 1.2, 0.68, 2.6);
  transition: opacity 0.5s, transform 0.5s cubic-bezier(0.57, 1.2, 0.68, 2.6);
  transition: opacity 0.5s, transform 0.5s cubic-bezier(0.57, 1.2, 0.68, 2.6), -webkit-transform 0.5s cubic-bezier(0.57, 1.2, 0.68, 2.6);
  will-change: transform, opacity
}

.nav-panel.invisible {
    opacity: 0;
    -webkit-transform: translateY(-50%) scale(0.5);
        -ms-transform: translateY(-50%) scale(0.5);
            transform: translateY(-50%) scale(0.5)
}

.nav-panel ul {
    list-style-type: none
}

.nav-panel ul li {
      position: relative;
      overflow: hidden;
      width: 1em;
      height: 1em;
      margin-bottom: 0.5em;
      border: 0.12em solid #000;
      border-radius: 50%;
      cursor: pointer;
      -webkit-transition: border-color, -webkit-transform 0.3s;
      transition: border-color, -webkit-transform 0.3s;
      -o-transition: border-color, transform 0.3s;
      transition: border-color, transform 0.3s;
      transition: border-color, transform 0.3s, -webkit-transform 0.3s;
      will-change: border-color, transform
}

.nav-panel ul li:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        -webkit-transform: translateX(-50%) translateY(-50%) scale(0.3);
            -ms-transform: translateX(-50%) translateY(-50%) scale(0.3);
                transform: translateX(-50%) translateY(-50%) scale(0.3);
        background-color: #000;
        opacity: 0;
        -webkit-transition: opacity 0.3s, -webkit-transform;
        transition: opacity 0.3s, -webkit-transform;
        -o-transition: transform, opacity 0.3s;
        transition: transform, opacity 0.3s;
        transition: transform, opacity 0.3s, -webkit-transform;
        will-change: transform, opacity
}

.nav-panel ul li.active:after {
        -webkit-transform: translateX(-50%) translateY(-50%) scale(0.7);
            -ms-transform: translateX(-50%) translateY(-50%) scale(0.7);
                transform: translateX(-50%) translateY(-50%) scale(0.7);
        opacity: 1;
        background-color: #000
}

.nav-panel ul li:hover:after {
          -webkit-transform: translateX(-50%) translateY(-50%) scale(0.7);
              -ms-transform: translateX(-50%) translateY(-50%) scale(0.7);
                  transform: translateX(-50%) translateY(-50%) scale(0.7);
          opacity: 1;
          background-color: #000;
          background-color: #000;
        }

.nav-panel ul li:hover {
        border-color: #000;
        -webkit-transform: scale(1.2);
            -ms-transform: scale(1.2);
                transform: scale(1.2);
        background-color: #000
}

.nav-panel .scroll-btn {
    position: absolute;
    left: 0;
    width: 1em;
    height: 1em;
    border: 0.2em solid #000;
    border-left: none;
    border-bottom: none;
    cursor: pointer;
    -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transition: border-color 0.3s;
    -o-transition: border-color 0.3s;
    transition: border-color 0.3s
}

.nav-panel .scroll-btn.up {
      top: -1.6em;
      -webkit-transform: rotate(-45deg);
          -ms-transform: rotate(-45deg);
              transform: rotate(-45deg)
}

.nav-panel .scroll-btn.down {
      bottom: -1.2em;
      -webkit-transform: rotate(135deg);
          -ms-transform: rotate(135deg);
              transform: rotate(135deg)
}

.nav-panel .scroll-btn:hover {
      border-color: #000
}

/* NAV BUTTONS */

.page1 {
  padding: 1.6rem;
}

